.main {
  margin-top: 2rem;
}

.title {
  color: var(--color-gray-500);
}

.tagCloud {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 1rem;
  background-image: url("/images/tags-heatmap.png");
  background-size: cover;
  background-position: center;
  border-radius: 10px;
  padding: 1rem;
  min-height: 400px;
}

.tagItem {
  cursor: pointer;
  font-weight: 500;
  transition: background-color 0.2s, transform 0.2s;
  background-color: rgba(255, 255, 255, 0.7);
  color: var(--color-gray-400);
  padding: 0.2rem 0.5rem;
  border-radius: 6px;
  white-space: nowrap;
  display: flex;
  align-items: center;
  gap: 0.2rem;
}

.tagItem:hover {
  background-color: rgba(220, 220, 220, 0.8);
  transform: scale(1.1);
}

.icon {
  width: 1em;
  height: 1em;
}
